import { useParams } from "react-router";
import { useArray } from "../../utils/hooks/use_array";
import { useState } from "react";
import { DisplayRoutes } from "../reusable_components/create_booking/display_routes";
import { SearchRouteInputField } from "../reusable_components/create_booking/search_route_input_field";
import { useRoutesHook } from "../../utils/hooks/use_routes_hook";
import { Button } from "../reusable_components/custom_button";
import { useComputeDistance } from "../../utils/hooks/use_compute_distance";
import { ThreeDots } from "react-loader-spinner";
import { useMutation, useQuery, useQueryClient } from "react-query";
import { fetchBookingFunction, updateBookingFunction } from "../../api/api";
import { LoadingComponent } from "../reusable_components/loading-content";
import { ErrorAlert, SuccessAlert } from "../reusable_components/alerts/alerts";
import { LoadPageAnimation } from "../reusable_components/load_page_animation";

export default function UpdateRoutes() {
  const { id } = useParams();
  const queryClient = useQueryClient();
  const [disableUserInterface, setDisableUserInterface] = useState(false);
  const useArrayOutput = useArray([]);
  const useRoutesOutput = useRoutesHook(useArrayOutput.push);
  const {
    // data,
    isComputeDistanceLoading,
    // isComputeDistanceSuccess,
    isComputeDistanceError,
    computeDistanceError,
    computeDistanceAsync,
  } = useComputeDistance(setDisableUserInterface);

  const {
    data: booking,
    error: bookingErrors,
    isError: isBookingError,
    isLoading: isBookingLoading,
    // isSuccess: isBookingSuccess,
  } = useQuery({
    queryKey: ["fetch-update-routes"],
    queryFn: () => fetchBookingFunction(id),
    onSuccess: (data) => {
      useArrayOutput.set(data.pickup_dropoff_routes);
      // console.log(data.pickup_dropoff_routes, "this is it mos");
    },
  });

  const {
    isLoading: isUpdateLoading,
    isSuccess: isUpdateSucces,
    mutateAsync: upadateBookingAsync,
    isError: isUpdateError,
    error: updateError,
  } = useMutation("update-routes", {
    mutationFn: updateBookingFunction,
    onSuccess: (event) => {
      setDisableUserInterface((s) => false);
      queryClient.invalidateQueries(["fetch-update-routes"]);
    },
    onMutate: (event) => {
      setDisableUserInterface((l) => true);
    },
    onError: (event) => {
      setDisableUserInterface((e) => false);
    },
  });

  const handleOnClick = async () => {
    if (useArrayOutput.array.length < 1) return;
    try {
      await computeDistanceAsync({
        pickup_dropoff_routes: useArrayOutput.array,
      }).then(async (response) => {
        try {
          const data = {
            id: booking.id,
            distance: Number(response["distance"]),
            pickup_dropoff_routes: useArrayOutput.array,
          };
          await upadateBookingAsync(data);
        } catch (error) {}
      });
    } catch (error) {}
  };

  if (isBookingLoading) {
    return <LoadingComponent secondary_text={"loading booking vehicle type"} />;
  }

  if (isBookingError) {
    return <ErrorAlert secondaryText={`${bookingErrors.response.data}`} />;
  }
  return (
    <LoadPageAnimation>
      <main className="max-w-xl mx-auto p-6 my-6 text-black bg-white">
        <div className="rounded-md p-6 shadow-sm border border-gray-200">
          <div className="mb-4">
            <h2 className="text-start text-black px-2 font-extrabold text-3xl">
              Update Pick up and drop off locations
              <p className="text-sm font-normal text-slate-800 pt-2">
                Trust matols logistics services to transport your items safely.
              </p>
              <p className="text-sm font-bold pt-2">Booking {id}</p>
            </h2>
          </div>
          <DisplayRoutes
            useArrayOutput={useArrayOutput}
            disableUserInterface={disableUserInterface}
          />
          <SearchRouteInputField
            useRoutesOutput={useRoutesOutput}
            disableUserInterface={disableUserInterface}
            setDisableUserInterface={setDisableUserInterface}
          />

          <div>
            {isComputeDistanceError && (
              <ErrorAlert
                className="mb-4"
                secondaryText={`${computeDistanceError.response.data.message} ${computeDistanceError.message}`}
              />
            )}

            {isUpdateError && (
              <ErrorAlert
                className="mb-4"
                secondaryText={`${updateError.response.data.message} ${updateError.message}`}
              />
            )}
            {isUpdateSucces && (
              <SuccessAlert
                secondaryText={
                  "Booking pick up and drop off locations successfully updated"
                }
                className="mb-4"
              />
            )}
          </div>
          <div className="px-2 py-4 flex justify-end">
            <Button
              disabled={useArrayOutput.array.length < 1}
              className="w-auto"
              onClick={handleOnClick}
            >
              {isComputeDistanceLoading || isUpdateLoading ? (
                <div className="w-full h-full flex justify-center items-center">
                  <ThreeDots visible width="40" height="20" color="#000" />
                </div>
              ) : (
                "Update"
              )}
            </Button>
          </div>
        </div>
      </main>
    </LoadPageAnimation>
  );
}
